.recipe {
    border-radius: 20px;
    margin: 20px;
    padding: 20px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    min-width: 40%;
    background: white;
}

.picture {
    margin: 20px 0;
    height: 150px;
    width: 150px;
    border-radius: 50%;
    
}

.food {
    font-size: 30px;
}

.item {
    font-size: 15px;
}